import request from "./request";
//用户添加地址接口
export const userAddAddress = (addList) => {
  return request({
    url: "/api/add/userAdd",
    method: "post",
    data: {
      addList,
    },
  });
};
//获取用户地址数据
export const userAddList = () => {
  return request({
    url: "/api/add/userAddList",
    method: "get",
  });
};
//用户修改收获地址
export const userChangeList = (id, changeList) => {
  return request({
    url: "/api/add/changeAddlist",
    method: "post",
    data: {
      id,
      changeList,
    },
  });
};
//用户删除收获地址信息
export const userDeleteAddList = (id) => {
  return request({
    url: "/api/add/deleteAddress",
    method: "get",
    params: {
      id,
    },
  });
};
