<template>
  <div class="add">
    <header>
      <i class="iconfont icon-fanhui" @click="backNextPage"></i>
      <span>我的地址</span>
      <i class="iconfont icon-gengduo2"></i>
    </header>
    <section>
      <div class="userDZ" v-for="(item, index) in useraddlist" :key="index">
        <div class="userDZ_l" @click="clickThisDZ(item)">
          <ul>
            <li>
              <span>{{ item.name }}</span> <span>{{ item.tel }}</span>
            </li>
            <li>
              <b v-show="item.isDefault == 1">[默认]</b
              ><span style="font-size: 0.4rem">{{ item.cityCounty }}</span
              ><span style="font-size: 0.4rem">{{ item.addressDetail }}</span>
            </li>
          </ul>
        </div>
        <div class="userDZ_r">
          <i
            class="iconfont icon-shezhi"
            @click="goChangeAddress(item)"
            v-show="!pathStatus"
          ></i>
        </div>
      </div>
      <button @click="goAddList">添加地址</button>
    </section>
    <Tabber class="Tabber"></Tabber>
  </div>
</template>

<script>
import Tabber from "@/components/common/Tabber";
import { userAddList } from "@/api/useraddress.js";
import { mapState, mapMutations } from "vuex";
// import Vue from 'vue';
// import { Dialog } from 'vant';
// Vue.use(Dialog);
import { Dialog } from "vant";
export default {
  name: "address-vue",
  components: {
    Tabber,
    // [Dialog.Component.name]: Dialog.Component,
  },
  data() {
    return {
      pathStatus: false, //判断用户是从那个路由组件跳转过来的，如果是从订单页面跳转过来的，则开启部分功能
    };
  },
  created() {
    this.getUserAddList();
    if (this.$route.params.path) {
      this.pathStatus = true;
    } else {
      this.pathStatus = false;
    }
  },
  activated() {
    if (this.$route.params.path) {
      this.pathStatus = true;
    } else {
      this.pathStatus = false;
    }
  },
  computed: {
    ...mapState({
      useraddlist: (state) => state.userAddress.useraddlist,
    }),
  },
  methods: {
    ...mapMutations(["USER_ADDLIST"]),
    backNextPage() {
      if (this.pathStatus) {
        this.$router.push("/order");
      } else {
        this.$router.push("/my");
      }
    },

    goAddList() {
      this.$router.push({
        name: "AddPathList",
        params: {
          pathStatus: this.pathStatus,
        },
      });
    },
    //用户修改地址回调
    goChangeAddress(item) {
      this.$router.push({
        name: "AddPathList",
        params: {
          item,
        },
      });
    },
    //获取用户地址数据
    async getUserAddList() {
      let res = await userAddList();
      if (res.data.code == 200) {
        this.USER_ADDLIST(res.data.data.r);
      }
    },
    //用户选择当前地址作为默认地址的回调
    clickThisDZ(item) {
      if (this.pathStatus) {
        Dialog.confirm({
          message: "确定将该地址设置为收获地址",
        }).then(() => {
          setTimeout(() => {
            this.$router.push({
              name: "Order",
              params: {
                item: JSON.stringify(item),
              },
            });
          }, 1000);
        });
      }
    },
  },
};
</script>

<style scoped lang="less">
.add {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
header {
  box-sizing: border-box;
  width: 100%;
  height: 8%;
  background-color: #b0352f;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.5rem;
  i {
    color: #fff;
    font-size: 0.7rem;
    font-weight: 500;
  }
  span {
    text-align: center;
    color: #fff;
    font-size: 0.5rem;
    font-weight: 500;
  }
}

section {
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
  background-color: #f5f5f5;
  font-size: 0.32rem;
  padding: 0 0.5rem;
  button {
    margin-top: 0.8rem;
    width: 3.5rem;
    height: 1rem;
    background-color: #b0352f;
    color: #fff;
    font-size: 0.6rem;
    border: 0;
  }
  .userDZ {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 2rem;
    background-color: #fff;
    padding: 0 0.3rem;
    margin-top: 0.3rem;
    .userDZ_l {
      padding-left: 0.3rem;
      width: 85%;
      height: 100%;
      ul {
        width: 100%;
        height: 100%;
        li {
          width: 100%;
          height: 50%;
          line-height: 1rem;
          font-size: 0.5rem;
          font-weight: 500;
          b {
            font-weight: 500;
            color: red;
            margin-right: 0.3rem;
          }
          span {
            margin-right: 0.2rem;
          }
        }
      }
    }
    .userDZ_r {
      height: 100%;
      width: 10%;
      i {
        font-size: 0.7rem;
        font-weight: 500;
        line-height: 2rem;
      }
    }
  }
}
</style>
